import React, { useState, useEffect } from 'react';
import '../styles/DafifCSS.css';
import ConnectingAirportsIcon from '@mui/icons-material/ConnectingAirports';

// Helper function to format dates as 'dd MMM yyyy'
function formatDate(date) {
  const day = date.getDate().toString().padStart(2, '0');
  const month = date.toLocaleString('en-GB', { month: 'short' }).slice(0, 3).toUpperCase();
  const year = date.getFullYear();

  return `${day} ${month} ${year}`;
}

function getAiracCycleInfo(startDate) {
  const initialDate = new Date(startDate);
  const cycleDuration = 28; // AIRAC cycle duration in days
  const oneDay = 24 * 60 * 60 * 1000; // One day in milliseconds

  const currentDate = new Date();
  let cycleNumber = 0;
  let cycleStartDate = new Date(initialDate);

  while (cycleStartDate <= currentDate) {
    cycleStartDate = new Date(cycleStartDate.getTime() + cycleDuration * oneDay);
    cycleNumber++;
  }

  const currentCycleStart = new Date(cycleStartDate.getTime() - (cycleDuration * oneDay) + oneDay);
  const cycleYear = currentCycleStart.getFullYear();
  const currentCycleYearStartDate = new Date(`${cycleYear}-01-01`);
  const daysSinceYearStart = Math.floor((currentCycleStart - currentCycleYearStartDate) / oneDay);
  const currentCycleIndex = Math.floor(daysSinceYearStart / cycleDuration) + 1;
  const cycleCode = `${cycleYear.toString().slice(-2)}${String(currentCycleIndex).padStart(2, '0')}`;

  const effectiveDate = currentCycleStart;
  const expirationDate = new Date(effectiveDate.getTime() + (cycleDuration * oneDay) - oneDay);

  return {
    airacCycleCode: cycleCode,
    effectiveDate: formatDate(effectiveDate),
    expirationDate: formatDate(expirationDate),
  };
}

const AiracCycleInfo = () => {
  const [airacInfo, setAiracInfo] = useState({
    airacCycleCode: '',
    effectiveDate: '',
    expirationDate: ''
  });

  useEffect(() => {
    const startDate = '2024-01-25'; // Correct AIRAC cycle start date for 2024

    const updateAiracCycleInfo = () => {
      const info = getAiracCycleInfo(startDate);
      setAiracInfo(info);
    };

    // Initial fetch
    updateAiracCycleInfo();

    // Function to calculate the milliseconds until the next midnight
    const calculateMillisecondsUntilMidnight = () => {
      const now = new Date();
      const nextMidnight = new Date(now);
      nextMidnight.setHours(24, 0, 0, 0); // Sets the time to the next midnight (00:00)
      return nextMidnight.getTime() - now.getTime();
    };

    // Set a timeout to run updateAiracCycleInfo at the next midnight
    const timeout = setTimeout(() => {
      updateAiracCycleInfo(); // Update once at midnight

      // Then set an interval to update every 24 hours thereafter
      const interval = setInterval(updateAiracCycleInfo, 24 * 60 * 60 * 1000);

      // Clear the interval if the component unmounts
      return () => clearInterval(interval);
    }, calculateMillisecondsUntilMidnight());

    // Clear the timeout if the component unmounts
    return () => clearTimeout(timeout);
  }, []);

  return (
    <div id="container">
      <div className="header-wrapper">
        <h1>DAFIF</h1>
        <ConnectingAirportsIcon />
      </div>
      <div id="cycle-code">Cycle: {airacInfo.airacCycleCode}</div>
      <div id="effective-date">Effective Date: {airacInfo.effectiveDate}</div>
      <div id="expiration-date">Expiration Date: {airacInfo.expirationDate}</div>
    </div>
  );
};

export default AiracCycleInfo;