import React, { useState, useEffect } from "react";
import '../styles/WeatherC.css';

const WeatherWidget = () => {
  const apiKey = process.env.REACT_APP_API_KEY;

  // State to store current weather data, forecast data, and user location
  const [weatherData, setWeatherData] = useState(null);
  const [forecast, setForecast] = useState([]);
  const [currentDayIndex, setCurrentDayIndex] = useState(0);
  const [lastFetchedDate, setLastFetchedDate] = useState(new Date().getDate());
  const [userLocation, setUserLocation] = useState(null);
  const [loading, setLoading] = useState(true);
  const [locationError, setLocationError] = useState("");

  // Utility function to get the day of the week from a date string
  const getDayOfWeek = (dateStr) => {
    const date = new Date(dateStr);
    const options = { weekday: 'long' };
    return date.toLocaleDateString('en-US', options);
  };

  // Utility function to get background gradient based on weather description
  const getBackgroundGradient = (description) => {
    switch (description) {
      case 'clear sky':
        return 'linear-gradient(to right, #00a8f9, #d6ecff)';
      case 'few clouds':
        return 'linear-gradient(to right, #3bc6f5, #efecec)';
      case 'scattered clouds':
        return 'linear-gradient(to right, #aed4f5, #e4e7e6)';
      case 'overcast clouds':
        return 'linear-gradient(to right, #4e7693, #e4e7e6)';
      case 'broken clouds':
        return 'linear-gradient(to right, #b0c4de, #f7f4f4)';
      case 'shower rain':
      case 'light rain':
      case 'moderate rain':
      case 'heavy intensity rain':
        return 'linear-gradient(to right, #4e7693, #7f8c8d)';
      case 'rain':
        return 'linear-gradient(to right, #3a4a5a, #7f8c8d)';
      case 'snow':
        return 'linear-gradient(to right, #e5ebed, #dff3f8)';
      case 'mist':
        return 'linear-gradient(to right, #f0f8ff, #e0e0e0)';
      default:
        return '#b2bdc5';
    }
  };

  // Function to display forecast data for a specific day
  const displayForecastDay = (forecasts, index) => {
    if (forecasts.length === 0) return;

    const forecastDay = forecasts[index];
    const temp = Math.ceil(forecastDay.main.temp);
    const icon = forecastDay.weather[0].icon;
    const weatherIconUrl = `https://openweathermap.org/img/wn/${icon}@2x.png`;

    const forecastElement = document.getElementById('forecast');
    forecastElement.innerHTML = '';

    const forecastDayDiv = document.createElement('div');
    forecastDayDiv.classList.add('forecast-day');

    const forecastDayName = document.createElement('span');
    const dateStr = forecastDay.dt_txt;
    forecastDayName.innerText = getDayOfWeek(dateStr);
    forecastDayDiv.appendChild(forecastDayName);

    const forecastIcon = document.createElement('img');
    forecastIcon.src = weatherIconUrl;
    forecastDayDiv.appendChild(forecastIcon);

    const forecastTemp = document.createElement('span');
    forecastTemp.classList.add('forecast-temp');
    forecastTemp.innerText = `${temp}°F`;
    forecastDayDiv.appendChild(forecastTemp);

    forecastElement.appendChild(forecastDayDiv);
  };

  // Function to fetch current weather data based on latitude and longitude  
  const fetchCurrentWeather = (lat, lon) => {
    const currentWeatherUrl = `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lon}&appid=${apiKey}&units=imperial`;

    fetch(currentWeatherUrl)
      .then(response => response.json())
      .then(data => setWeatherData(data))
      .catch(error => console.error('Error fetching current weather:', error));
  };

  // Function to fetch 5-day weather forecast based on latitude and longitude
  const fetchForecastData = (lat, lon) => {
    const forecastUrl = `https://api.openweathermap.org/data/2.5/forecast?lat=${lat}&lon=${lon}&appid=${apiKey}&units=imperial`;

    fetch(forecastUrl)
      .then(response => response.json())
      .then(data => {
        const today = new Date();
        const forecasts = data.list.filter(item => {
          const forecastDate = new Date(item.dt_txt);
          return forecastDate.getDate() > today.getDate() || 
                 (forecastDate.getDate() === today.getDate() && forecastDate.getHours() >= 12);
        });

        const nextDaysForecast = [];
        let currentDay = today.getDate();
        for (let i = 0; i < forecasts.length; i++) {
          const forecastDate = new Date(forecasts[i].dt_txt);
          if (forecastDate.getDate() !== currentDay) {
            nextDaysForecast.push(forecasts[i]);
            currentDay = forecastDate.getDate();
          }
          if (nextDaysForecast.length === 4) break;
        }

        setForecast(nextDaysForecast);
        setLastFetchedDate(today.getDate());
        setCurrentDayIndex(0);
      })
      .catch(error => console.error('Error fetching 5-day forecast:', error));
  };

  // Hook to get user's current location
  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
          setUserLocation({ latitude, longitude });
          setLoading(false); // Location successfully retrieved
        },
        (error) => {
          console.error('Error getting user location:', error);
          setLocationError("Could not get location. Please enable location services.");
          setLoading(false); // Stop loading when an error occurs
        }
      );
    } else {
      console.error('Geolocation is not supported by this browser.');
      setLocationError("Geolocation is not supported by this browser.");
      setLoading(false);
    }
  }, []);

  // Hook to fetch weather and forecast data when user location is set 
  useEffect(() => {
    if (userLocation) {
      fetchCurrentWeather(userLocation.latitude, userLocation.longitude);
      fetchForecastData(userLocation.latitude, userLocation.longitude);

      const weatherInterval = setInterval(() => {
        fetchCurrentWeather(userLocation.latitude, userLocation.longitude);
      }, 900000);

      return () => clearInterval(weatherInterval);
    }
  }, [userLocation]);

  // Hook to rotate forecast days
  useEffect(() => {
    const forecastInterval = setInterval(() => {
      setCurrentDayIndex(prevIndex => (prevIndex + 1) % forecast.length);
    }, 10000);

    return () => clearInterval(forecastInterval);
  }, [forecast]);

  useEffect(() => {
    if (forecast.length > 0) {
      displayForecastDay(forecast, currentDayIndex);
    }
  }, [currentDayIndex, forecast]);

  // Hook to check the date and refresh the forecast if a new day starts
  useEffect(() => {
    const dateCheckInterval = setInterval(() => {
      const today = new Date().getDate();
      if (today !== lastFetchedDate && userLocation) {
        fetchForecastData(userLocation.latitude, userLocation.longitude);
      }
    }, 60000);

    return () => clearInterval(dateCheckInterval);
  }, [lastFetchedDate, userLocation]);

  // Hook to update background based on weather data
  useEffect(() => {
    const updateBackground = () => {
      if (weatherData) {
        const currentTime = new Date().getTime() / 1000;
        const isNight = currentTime >= weatherData.sys.sunset || currentTime <= weatherData.sys.sunrise;
        const description = weatherData.weather[0].description;
        const gradient = isNight ? null : getBackgroundGradient(description);
        const widgetElement = document.getElementById('weather-widget');

        if (isNight) {
          widgetElement.classList.add('night');
          widgetElement.classList.remove('day');
          widgetElement.style.background = '';
        } else {
          widgetElement.classList.add('day');
          widgetElement.classList.remove('night');
          widgetElement.style.background = gradient;
        }
      }
    };

    const logBackgroundInfo = () => {
      if (weatherData) {
        const currentTime = new Date().getTime() / 1000;
        const sunsetTime = weatherData.sys.sunset;
        const sunriseTime = weatherData.sys.sunrise;
        const isNight = currentTime >= sunsetTime || currentTime <= sunriseTime;
      }
    };

    updateBackground();

    const weatherUpdateInterval = setInterval(updateBackground, 2000);
    const loggingInterval = setInterval(logBackgroundInfo, 300000);

    return () => {
      clearInterval(weatherUpdateInterval);
      clearInterval(loggingInterval);
    };
  }, [weatherData]);

  // Show loading message or location error if location data is unavailable
  if (loading) return <div className="loading">Loading...</div>;
  if (locationError) return <div className="error">{locationError}</div>;
  if (!weatherData) return <div className="loading">Loading weather data...</div>;

  // Extract data from weatherData for rendering
  const temp = Math.ceil(weatherData.main.temp);
  const weatherDescription = weatherData.weather[0].description;
  const icon = weatherData.weather[0].icon;
  const weatherIconUrl = `https://openweathermap.org/img/wn/${icon}@2x.png`;

  return (
    <div id="weather-widget">
      <div id="today-weather">
        <h2>Today</h2>
        <img id="weather-icon" src={weatherIconUrl} alt="Weather Icon" />
        <div id="weather-temp-description">
          <div id="weather-temp">{temp}°F</div>
          <div id="weather-description">{weatherDescription}</div>
        </div>
      </div>
      <div id="forecast"></div>
    </div>
  );
};

export default WeatherWidget;

